$poppins: 'Poppins', sans-serif;
$gutters: 10px;
$color-primary: #439CE2;
$color-secondary: #999999;

@mixin font-size($size) {
  font-size: $size/19.2;
  @if $size == 20vw {
    @media screen and (min-width: 1920px) {
      font-size: 20px;
    }
    @media screen and (max-width: 1366px) {
      font-size: 14px;
    }
  }
}

body {
  color: $color-secondary;
  font-family: $poppins;
  font-weight: 400;
  position: relative;
}

textarea {
  resize: vertical;
}

::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: #C0C0C0 !important;
  opacity: 1; /* Firefox */
}

:-ms-input-placeholder { /* Internet Explorer 10-11 */
  color: #C0C0C0 !important;
}

::-ms-input-placeholder { /* Microsoft Edge */
  color: #C0C0C0 !important;
}

.background {
  position: absolute;
  left: (100vw/19.2);
  width: (305vw/19.2);
  height: (305vw/19.2);
  min-width: 120px;
  min-height: 120px;
  z-index: 0;
  top: 55%;
  top: 28rem;

  svg, img {
    width: (305vw/19.2);
    height: auto;
    min-width: 120px;
  }
}


.custom-container {
  max-width: 812px;
  margin: auto;
  z-index: 1;
  position: relative;
}

.row.custom {
  margin-right: -$gutters;
  margin-left: -$gutters;

  .col, .col-1, .col-10, .col-11, .col-12, .col-2, .col-3, .col-4, .col-5, .col-6, .col-7, .col-8, .col-9, .col-auto, .col-lg, .col-lg-1, .col-lg-10, .col-lg-11, .col-lg-12, .col-lg-2, .col-lg-3, .col-lg-4, .col-lg-5, .col-lg-6, .col-lg-7, .col-lg-8, .col-lg-9, .col-lg-auto, .col-md, .col-md-1, .col-md-10, .col-md-11, .col-md-12, .col-md-2, .col-md-3, .col-md-4, .col-md-5, .col-md-6, .col-md-7, .col-md-8, .col-md-9, .col-md-auto, .col-sm, .col-sm-1, .col-sm-10, .col-sm-11, .col-sm-12, .col-sm-2, .col-sm-3, .col-sm-4, .col-sm-5, .col-sm-6, .col-sm-7, .col-sm-8, .col-sm-9, .col-sm-auto, .col-xl, .col-xl-1, .col-xl-10, .col-xl-11, .col-xl-12, .col-xl-2, .col-xl-3, .col-xl-4, .col-xl-5, .col-xl-6, .col-xl-7, .col-xl-8, .col-xl-9, .col-xl-auto {
    padding-left: $gutters;
    padding-right: $gutters;
  }
}

.btn-main {
  border: 1px solid $color-primary;
  border-radius: 8px;
  @include font-size(20vw);
  color: $color-primary;
  padding: 12px 0.75rem;
  transition: all 0.2s ease-out;
  &:hover {
    background-color: $color-primary;
    color: white;
  }
}

.btn-icon {
  svg {
    fill: $color-primary;
    height: (25vw/19.2);
    max-height: 25px;
    min-height: 15px;
    width: auto;
    margin: 0 5px;
    transition: all 0.2s ease-out;
  }

  span {
    margin: 0 5px;
  }
  &:hover{
    svg{
      fill: white;
    }
  }
}

.header {
  background-image: url("../images/map.svg");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}

.profile-picture {
  max-height: 160px;
  max-width: 160px;
  min-width: 100px;
  min-height: 100px;
  width: (160vw/19.2);
  height: (160vw/19.2);
  border-radius: (36vw/19.2);
  overflow: hidden;
  position: relative;

  img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  @media screen and (min-width: 1920px) {
    border-radius: 36px;
  }
}

.profile-name {
  text-align: center;

  .name {
    font-size: (34vw/19.2);
    color: #29275F;
    font-family: $poppins;
    font-weight: 600;
    @media screen and (min-width: 1920px) {
      font-size: 34px;
    }
    @media screen and (max-width: 1366px) {
      font-size: 24px;
    }
  }

  .position {
    @include font-size(20vw);
  }
}

.social-icons {
  flex-wrap: wrap;
  margin: 0.5rem (-16vw/19.2);

  .social-icon {
    height: (50vw/19.2);
    width: (50vw/19.2);
    max-height: 50px;
    max-width: 50px;
    min-width: 30px;
    min-height: 30px;
    background-color: #C0C0C0;
    overflow: hidden;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 (16vw/19.2);
    position: relative;
    &:before{
      content: '';
      position: absolute;
      width: 0;
      height: 0;
      z-index: 1;
      transition: all 0.2s ease-out;
      border-radius: 50%;
    }
    &:hover{
      &:before{
        height: 50px;
        width: 50px;
      }
    }
    svg {
      height: (20vw/19.2);
      width: auto;
      max-height: 20px;
      min-height: 15px;
      fill: white;
      z-index: 2;
    }

    &.email, &.messenger {
      svg {
        height: auto;
        width: (18vw/19.2);
        max-width: 18px;
        min-width: 14px;
      }
    }
    &.contact:before{
      background-color: #333333;
    }
    &.telegram:before{
      background-color: #28BBCC;
    }
    &.twitter:before{
      background-color: #00ACEE;
    }
    &.whatsapp:before{
      background-color: #5ED468;
    }
    &.email:before{
      background-color: #D44638;
    }
    &.messenger:before{
      background-color: #4394F2;
    }
  }

  @media screen and (min-width: 1920px) {
    margin: 0 -16px;
    .social-icon {
      margin: 0 16px;
    }
  }
}

#tabs {
  background-color: rgba(248, 248, 248, 0.8);
  border-radius: 8px;

  .nav-item {
    border-radius: 8px;
    overflow: hidden;

    .nav-link {
      color: $color-secondary;
      font-family: $poppins;
      font-weight: 500;
      @include font-size(20vw);
      line-height: 1;
      padding: 16px 1rem;
      cursor: pointer;
      transition: all 0.2s ease-out;
      display: flex;
      justify-content: center;
      align-items: center;

      svg {
        fill: $color-secondary;
        height: (25vw/19.2);
        max-height: 25px;
        min-height: 15px;
        width: auto;
        transition: all 0.2s ease-out;
        margin: 0 5px;
      }

      span {
        margin: 0 5px;
        line-height: 1;
      }

      &.active {
        background: $color-primary 0 0 no-repeat padding-box;
        color: white;

        svg {
          fill: #fff;
        }
      }
    }
  }
}

.paper {
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.16);
  border-radius: 4px;
  background-color: white;
  margin: 12px auto;
}

.field {
  padding: (20vw/19.2);
  //display: block;
  color: $color-secondary;
  text-decoration: none;
  font-family: $poppins;

  &:hover {
    text-decoration: none;
    color: inherit;
  }

  &.flex {
    display: flex;
    justify-content: flex-start;
    align-items: normal;
  }

  span {
    @include font-size(20vw);
    margin: 0 5px;
    display: block;

    &.label {
      color: $color-primary;
      flex-shrink: 0;
    }

    &.title {
      color: #666666;
      margin-bottom: 10px;
    }
  }

  @media screen and (min-width: 1920px) {
    padding: 20px;
  }
  @media screen and (max-width: 1366px) {
    padding: 14px;
  }
}

.form-control {
  margin: 10px auto;
  @include font-size(20vw);
  height: auto;
  padding: 0.5rem 0.75rem;
  border-radius: 8px;
  line-height: 2;
}

textarea.form-control {
  line-height: 1.3;
}

#tabContent {
  margin-top: 20px;
}

.footer {
  @include font-size(20vw);
}

#company {
  .media-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;

    .icon {
      display: inline-block;
      position: relative;
      height: (68vw/19.2);
      width: (68vw/19.2);
      min-height: 60px;
      min-width: 60px;
      max-height: 68px;
      max-width: 68px;
      border: 1px solid #C0C0C0;

      img {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
      }

      .more {
        text-decoration: none;
        color: #333333;
      }
    }
  }
}

#profile{
  .progress{
    margin: 15px 5px 0;
    height: 10px;
    border-radius: 10px;
    background-color: #EFEFEF;
    .progress-bar{
      background-color: #439CE2;
    }
  }
}

